export default "import { Component, OnInit } from \"@angular/core\";\nimport {\n    BandScale, barAccessors, barGrid, BarHighlightStrategy, BarRenderer, barScales, Chart, CHART_PALETTE_CS1, IBarChartConfig, InteractionLinePlugin,\n    MappedValueProvider,\n} from \"@nova-ui/charts\";\nimport zipObject from \"lodash/zipObject\";\n\n@Component({\n    selector: \"nui-waterfall-chart-simple-example\",\n    templateUrl: \"./waterfall-chart-simple.example.component.html\",\n})\nexport class WaterfallChartSimpleComponent implements OnInit {\n    // Step 1 - Create a new horizontal bar chart\n    private chartConfig: IBarChartConfig = { horizontal: true };\n    public chart = new Chart(barGrid(this.chartConfig));\n\n    public ngOnInit() {\n        const accessors = barAccessors(this.chartConfig);\n        const scales = barScales(this.chartConfig);\n\n        // Step 1 - Optionally, add interaction highlighting behavior.\n        // If highlighting behavior is not desired, the renderer can be instantiated without a 'highlightStrategy' configuration\n        // and the InteractionLinePlugin registration can be skipped.\n        const renderer = new BarRenderer({ highlightStrategy: new BarHighlightStrategy(\"x\") });\n        this.chart.addPlugin(new InteractionLinePlugin());\n\n        // Step 2 - Create a color provider that maps the statuses (or types) of categories to their corresponding colors\n        const colorProvider = new MappedValueProvider(zipObject([\"connect\", \"dns\", \"send\", \"ttfb\", \"cdownload\"], CHART_PALETTE_CS1));\n\n        // Step 3 - Adjust the color accessor, to retrieve the color or the bar by the corresponding data type.\n        accessors.data.color = (d) => colorProvider.get(d.type);\n\n        // Step 4 - Configure the format of the bottom label by setting custom scales.x.formatter function.\n        scales.x.formatters.tick = (value: number) => `${Number(value / 1000).toFixed(1)}s`;\n\n        // Step 5 - Configure the thickness of the bar using the BandScale.padding method on your scales.y.\n        (<BandScale>scales.y).padding(0.5);\n\n        this.chart.update(getData().map(s => ({\n            ...s,\n            accessors,\n            scales,\n            renderer,\n        })));\n    }\n}\n\n/** Chart Data */\nfunction getData() {\n    return [\n        {\n            id: \"1\",\n            name: \"Category 1\",\n            data: [\n                {\n                    type: \"connect\",\n                    start: 0, // in ms\n                    end: 22,\n                },\n                {\n                    type: \"dns\",\n                    start: 22,\n                    end: 39,\n                },\n                {\n                    type: \"send\",\n                    start: 39,\n                    end: 59,\n                },\n                {\n                    type: \"ttfb\",\n                    start: 59,\n                    end: 109,\n                },\n                {\n                    type: \"cdownload\",\n                    start: 109,\n                    end: 178,\n                },\n            ],\n        }, {\n            id: \"2\",\n            name: \"Category 2\",\n            data: [\n                {\n                    type: \"connect\",\n                    start: 0, // in ms\n                    end: 22,\n                },\n                {\n                    type: \"dns\",\n                    start: 22,\n                    end: 39,\n                },\n                {\n                    type: \"send\",\n                    start: 39,\n                    end: 59,\n                },\n                {\n                    type: \"ttfb\",\n                    start: 59,\n                    end: 109,\n                },\n                {\n                    type: \"cdownload\",\n                    start: 109,\n                    end: 788,\n                },\n            ],\n        },\n        {\n            id: \"3\",\n            name: \"Category 3\",\n            data: [\n                {\n                    type: \"connect\",\n                    start: 178, // in ms\n                    end: 222,\n                },\n                {\n                    type: \"dns\",\n                    start: 222,\n                    end: 239,\n                },\n                {\n                    type: \"send\",\n                    start: 239,\n                    end: 259,\n                },\n                {\n                    type: \"ttfb\",\n                    start: 259,\n                    end: 309,\n                },\n                {\n                    type: \"cdownload\",\n                    start: 309,\n                    end: 578,\n                },\n            ],\n        },\n        {\n            id: \"4\",\n            name: \"Category 4\",\n            data: [\n                {\n                    type: \"connect\",\n                    start: 578, // in ms\n                    end: 590,\n                },\n                {\n                    type: \"dns\",\n                    start: 590,\n                    end: 799,\n                },\n                {\n                    type: \"send\",\n                    start: 799,\n                    end: 888,\n                },\n                {\n                    type: \"ttfb\",\n                    start: 888,\n                    end: 900,\n                },\n                {\n                    type: \"cdownload\",\n                    start: 900,\n                    end: 990,\n                },\n            ],\n        },\n    ];\n\n}\n";