export default "import { Component, OnInit } from \"@angular/core\";\nimport { barAccessors, barGrid, BarHighlightStrategy, BarRenderer, barScales, Chart, ChartAssist, IBarChartConfig, stack } from \"@nova-ui/charts\";\n\n@Component({\n    selector: \"nui-basic-stacked-horizontal-bar-chart-test\",\n    templateUrl: \"./basic-stacked-horizontal-bar-chart-test.component.html\",\n})\n\nexport class BasicStackedHorizontalBarChartTestComponent implements OnInit {\n    public chartAssist: ChartAssist;\n\n    ngOnInit() {\n        const config = { horizontal: true } as IBarChartConfig;\n\n        const chart = new Chart(barGrid(config));\n        this.chartAssist = new ChartAssist(chart, stack);\n\n        const accessors = barAccessors(config);\n        // This matches field in data by which chart preprocessor will gather categories to build stacks\n        accessors.data.category = (data: any) => data.name;\n\n        const renderer = new BarRenderer({ highlightStrategy: new BarHighlightStrategy(\"y\") });\n\n        const scales = barScales(config);\n\n        const seriesSet = getData().map(d => ({\n            ...d,\n            accessors,\n            renderer,\n            scales,\n        }));\n\n        this.chartAssist.update(seriesSet);\n    }\n}\n\n/* Chart data */\nfunction getData() {\n    return [\n        {\n            \"id\": \"Brno\",\n            \"name\": \"Brno\",\n            \"data\": [\n                {\n                    \"name\": \"Q1 2018\",\n                    \"value\": 167,\n                },\n                {\n                    \"name\": \"Q2 2018\",\n                    \"value\": 122,\n                },\n                {\n                    \"name\": \"Q3 2018\",\n                    \"value\": 141,\n                },\n                {\n                    \"name\": \"Q4 2018\",\n                    \"value\": 66,\n                },\n            ],\n        },\n        {\n            \"id\": \"Austin\",\n            \"name\": \"Austin\",\n            \"data\": [\n                {\n                    \"name\": \"Q1 2018\",\n                    \"value\": 167,\n                },\n                // Please note the fact that not all categories are required to be present in every data point.\n                // Sparse data is ok too.\n                // {\n                //     \"name\": \"Q2 2018\",\n                //     \"value\": 198,\n                // },\n                {\n                    \"name\": \"Q3 2018\",\n                    \"value\": 208,\n                },\n                {\n                    \"name\": \"Q4 2018\",\n                    \"value\": 233,\n                },\n            ],\n        },\n        {\n            \"id\": \"Edinburgh\",\n            \"name\": \"Edinburgh\",\n            \"data\": [\n                {\n                    \"name\": \"Q1 2018\",\n                    \"value\": 167,\n                },\n                {\n                    \"name\": \"Q2 2018\",\n                    \"value\": 15,\n                },\n                {\n                    \"name\": \"Q3 2018\",\n                    \"value\": 208,\n                },\n                {\n                    \"name\": \"Q4 2018\",\n                    \"value\": 123,\n                },\n            ],\n        },\n        {\n            \"id\": \"Newcastle\",\n            \"name\": \"Newcastle\",\n            \"data\": [\n                {\n                    \"name\": \"Q1 2018\",\n                    \"value\": 11,\n                },\n                {\n                    \"name\": \"Q2 2018\",\n                    \"value\": 99,\n                },\n                {\n                    \"name\": \"Q3 2018\",\n                    \"value\": 17,\n                },\n                {\n                    \"name\": \"Q4 2018\",\n                    \"value\": 25,\n                },\n            ],\n        },\n        {\n            \"id\": \"Kyiv\",\n            \"name\": \"Kyiv\",\n            \"data\": [\n                {\n                    \"name\": \"Q1 2018\",\n                    \"value\": 121,\n                },\n                {\n                    \"name\": \"Q2 2018\",\n                    \"value\": 222,\n                },\n                {\n                    \"name\": \"Q3 2018\",\n                    \"value\": 319,\n                },\n                {\n                    \"name\": \"Q4 2018\",\n                    \"value\": 328,\n                },\n            ],\n        },\n    ];\n}\n";