export default "import { Component, OnInit } from \"@angular/core\";\nimport {\n    BandScale, barAccessors, barGrid, BarHighlightStrategy, BarRenderer, Chart, ChartAssist, InteractionLabelPlugin, INTERACTION_DATA_POINTS_EVENT, LinearScale,\n    Scales,\n    SELECT_DATA_POINT_EVENT\n} from \"@nova-ui/charts\";\n\n@Component({\n    selector: \"nui-grouped-vertical-bar-chart-test\",\n    templateUrl: \"./grouped-vertical-bar-chart-test.component.html\",\n})\n\nexport class GroupedVerticalBarChartTestComponent implements OnInit {\n    public chartAssist: ChartAssist;\n    public accessors = barAccessors();\n\n    ngOnInit() {\n        const chart = new Chart(barGrid());\n\n        // We're manually adding Interaction Label plugin (without Interaction Line plugin) to have only label\n        chart.addPlugin(new InteractionLabelPlugin());\n\n        const bandScale = new BandScale();\n        bandScale.padding(0.25);\n        bandScale.innerScale = new BandScale();\n\n        const linearScale = new LinearScale();\n        const renderer = new BarRenderer({ highlightStrategy: new BarHighlightStrategy(\"x\") });\n\n        this.accessors.data.category = (data: any) => [data.name, data.subCategory];\n\n        this.chartAssist = new ChartAssist(chart);\n\n        const scales: Scales = {\n            x: bandScale,\n            y: linearScale,\n        };\n\n        const mappedSeries = getData().map(d => ({\n            ...d,\n            accessors: this.accessors,\n            renderer,\n            scales,\n        }));\n\n        // Sample events that can be used in order to handle click or highlighting of certain status\n        chart.getEventBus().getStream(INTERACTION_DATA_POINTS_EVENT).subscribe(console.log);\n        chart.getEventBus().getStream(SELECT_DATA_POINT_EVENT).subscribe(console.log);\n\n        this.chartAssist.update(mappedSeries);\n    }\n}\n\n/* Chart data */\nfunction getData() {\n    return [\n        {\n            \"id\": \"Brno\",\n            \"name\": \"Brno\",\n            \"data\": [\n                {\n                    \"name\": \"Q1 2018\",\n                    \"subCategory\": \"Brno\",\n                    \"value\": 167,\n                },\n                {\n                    \"name\": \"Q2 2018\",\n                    \"subCategory\": \"Brno\",\n                    \"value\": 122,\n                },\n                {\n                    \"name\": \"Q3 2018\",\n                    \"subCategory\": \"Brno\",\n                    \"value\": 141,\n                },\n                {\n                    \"name\": \"Q4 2018\",\n                    \"subCategory\": \"Brno\",\n                    \"value\": 66,\n                },\n            ],\n        },\n        {\n            \"id\": \"Austin\",\n            \"name\": \"Austin\",\n            \"data\": [\n                {\n                    \"name\": \"Q1 2018\",\n                    \"subCategory\": \"Austin\",\n                    \"value\": 167,\n                },\n                {\n                    \"name\": \"Q2 2018\",\n                    \"subCategory\": \"Austin\",\n                    \"value\": 198,\n                },\n                {\n                    \"name\": \"Q3 2018\",\n                    \"subCategory\": \"Austin\",\n                    \"value\": 208,\n                },\n                {\n                    \"name\": \"Q4 2018\",\n                    \"subCategory\": \"Austin\",\n                    \"value\": 233,\n                },\n            ],\n        },\n        {\n            \"id\": \"Edinburgh\",\n            \"name\": \"Edinburgh\",\n            \"data\": [\n                {\n                    \"name\": \"Q1 2018\",\n                    \"subCategory\": \"Edinburgh\",\n                    \"value\": 167,\n                },\n                // sparse data is handled as well\n                // {\n                //     \"name\": \"Q2 2018\",\n                //     \"subCategory\": \"Edinburgh\",\n                //     \"value\": 15,\n                // },\n                {\n                    \"name\": \"Q3 2018\",\n                    \"subCategory\": \"Edinburgh\",\n                    \"value\": 208,\n                },\n                {\n                    \"name\": \"Q4 2018\",\n                    \"subCategory\": \"Edinburgh\",\n                    \"value\": 123,\n                },\n            ],\n        },\n        {\n            \"id\": \"Newcastle\",\n            \"name\": \"Newcastle\",\n            \"data\": [\n                {\n                    \"name\": \"Q1 2018\",\n                    \"subCategory\": \"Newcastle\",\n                    \"value\": 11,\n                },\n                {\n                    \"name\": \"Q2 2018\",\n                    \"subCategory\": \"Newcastle\",\n                    \"value\": 99,\n                },\n                {\n                    \"name\": \"Q3 2018\",\n                    \"subCategory\": \"Newcastle\",\n                    \"value\": 17,\n                },\n                {\n                    \"name\": \"Q4 2018\",\n                    \"subCategory\": \"Newcastle\",\n                    \"value\": 25,\n                },\n            ],\n        },\n        {\n            \"id\": \"Kyiv\",\n            \"name\": \"Kyiv\",\n            \"data\": [\n                {\n                    \"name\": \"Q1 2018\",\n                    \"subCategory\": \"Kyiv\",\n                    \"value\": 121,\n                },\n                {\n                    \"name\": \"Q2 2018\",\n                    \"subCategory\": \"Kyiv\",\n                    \"value\": 222,\n                },\n                {\n                    \"name\": \"Q3 2018\",\n                    \"subCategory\": \"Kyiv\",\n                    \"value\": 319,\n                },\n                {\n                    \"name\": \"Q4 2018\",\n                    \"subCategory\": \"Kyiv\",\n                    \"value\": 328,\n                },\n            ],\n        },\n    ];\n}\n";